.header {
    width: 100vw;
    height: 70px;
    background-color: dodgerblue;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav{
    width: 80vw;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
}

.button{
    background-color: white;
}

.item {
    margin-right: 30px;
}

.item a {
    color: black;
    text-decoration: none;
    font-size: 18px;
    font-weight: bold;
}
.item a.activeLink {
    color: white;
}